<template>
  <div class="resume-new-work">
    <cell-group class="resume-new-work__item">
      <field
        label="公司名"
        placeholder="公司名"
      />
      <field
        label="职务"
        placeholder="职务"
      />
      
      <field
        label="开始时间"
        readonly
        v-model="startTime"
        @click="isShowStartTime=true"
      />
      <field
        label="公司名"
      />

      <datetime-picker
        v-if="isShowStartTime"
        v-model="startTime"
        @confirm="hideStartTime"
        @cancel="hideStartTime"
        type="year-month"
      />
    </cell-group>
  </div>  
</template>


<script>
import { Cell, CellGroup, Field, DatetimePicker } from "vant";
export default {
  data() {
    return {
      company: "",
      busines: "",
      startTime: "",

      // status
      isShowStartTime: false
    };
  },
  components: {
    CellGroup,
    Cell,
    Field,
    DatetimePicker
  },
  methods: {
    hideStartTime() {
      this.isShowStartTime = false;
    }
  }
};
</script>

<style lang="postcss" scoped>
.resume-new-work {
  overflow-x: hidden;
  overflow-y: auto;
  &__item {
    margin-top: 10px;
  }
}
</style>



